.carousel-item img{
  animation:zoom-in 80s ease infinite;
 
}

.slider-overlay{
 position: absolute;
 top: 0;
 bottom: 0;
 left: 0;
 right: 0;
 background-color: rgba(0, 0, 0, 0.75);
  
}
.slider-wrapper{
  z-index: -1;
  position: relative;
}
